@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
#carousel {
    position: relative;
    z-index: 1;
}

#carousel img {
    height: 400px;
}

#carousel  .overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    font-size: 20px;
    z-index: 2;
}

#carousel .overlay .texto1 {
    height: 90px;
    background-color: rgb(17, 16, 9);
    border-radius: 40px;
    opacity: 50%;;
}   

#carousel .overlay .texto1 h1 {
    font-size: 20px;
}


#carousel  .overlay .container,
#carousel  .overlay .row {
    height: 100%;
}
header {
    background-color: yellow;
}

header h1 {
    font-family: 'Anton', sans-serif;
    font-size: 1000%;
    text-shadow: 2px 2px black;
    color: #6ec8ec;
}

header h2 {
    font-family: 'Anton', sans-serif;
    font-size: 700%;
    line-height: 40%;
    text-shadow: 3px 1px black;
    color: #3EDBF0;
}

.container .row .card img {
    height: auto;
    width: auto;
    max-height: 250px;
    object-fit: contain;
}


.cards {
    height: 300px;
    object-fit: cover;

}
#footer {
    background-color: #6eceec;
}

.redes-sociales {
    height: 70px;
    padding-top: 10px;
    color: white;
}

.redes-sociales img {
    height: 40px;
}

.enlace-redes {
  text-align: center;
}

.enlace-redes strong {
    color: white;
    font-size: x-large;
}

#header {
    background: #6eceec;
    height: 40px;
}

#header #Nav .ml-auto #NavLink {
    color: white;
}

.logo img {
    height: 35px;
    margin-left: -9px;
    margin-top: -10px;
}

#Nav .main .selection {
    color: white;
}
